<template>
  <div class="login-page">
    <div class="login-box">
      <div class="card card-outline card-primary">
        <div class="card-header text-center">
          <p class="h1"><b style="font-size: 30px">智能考勤</b></p>
        </div>
        <div class="card-body">
          <div class="form-group">
            <label for="exampleInputEmail1">用户名</label>
            <div class="input-group mb-3">
              <div class="input-group-text">
                <span class="fas fa-envelope"></span>
              </div>
              <input type="text" class="form-control" placeholder="username" v-model="username">
            </div>
          </div>

          <div class="form-group">
            <label for="exampleInputEmail1">密码</label>
            <div class="input-group mb-3">
              <div class="input-group-text">
                <span class="fas fa-lock"></span>
              </div>
              <input type="password" class="form-control" placeholder="password" v-model="password">
            </div>
          </div>

          <div class="social-auth-links text-center mt-4 mb-2" @click="login">
            <a href="#" class="btn btn-block btn-primary">
              登录
            </a>
          </div>

          <p class="mb-1 float-right">
            <router-link to="addface">前往录入人脸信息</router-link>
          </p>

        </div>
      </div>
    </div>
  </div>
</template>

<script>
import API from "@/server/api";
export default {
  name: "Login",
  data() {
    return {
      username: "tester",
      password: "123456",
    };
  },
  mounted() {},
  methods: {
    login() {
      if (this.username != "" && this.password != "") {
        this.isLoging = true;
        this.$req
          .post(API.admin.login.path, {
            username: this.username,
            password: this.password,
          })
          .then((resp) => {
            if (resp.code == 1) {
              sessionStorage.setItem("adminInfo", JSON.stringify(resp.data));
              this.$router.push("/user/usermgr");
            }
          });
      }
    },
  },
};
</script>

<style scoped lang="scss">
.login-page {
  background-image: url(~@/assets/img/bt1.jpg);
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
}
.login-box {
  position: absolute;
  top: 18%;
  left: 66%;
  width: 426px;
  .card {
    width: 100%;
    height: 100%;
  }
}
</style>
